<template>
  <!-- <CHeader fixed with-subheader light> -->
  <CHeader fixed light>
    <CToggler
      in-header
      class="ml-3 d-lg-none"
      v-c-emit-root-event:toggle-sidebar-mobile
    />
    <CToggler
      in-header
      class="ml-3 d-md-down-none"
      v-c-emit-root-event:toggle-sidebar
    />
    <CHeaderBrand
      class="mx-auto d-lg-none" 
      src="img/brand/coreui-vue-logo.svg"
      width="190"
      height="46"
      alt="CoreUI Logo"
    />
    <CHeaderNav class="d-md-down-none mr-auto">
      <CHeaderNavItem v-if="bShow" class="d-md-down-none mx-1">
        <h3><CBadge color="warning">{{$store.state.line_name}}</CBadge> <font-awesome-icon class="pt-1" icon="angle-double-right"/></h3>
      </CHeaderNavItem>
      <CHeaderNavItem v-if="bShow" class="d-md-down-none mx-1">
        <h3 v-if="status==0" class="blink_me"><CBadge color="success">稼動中</CBadge></h3>
        <h3 v-else class="blink_me"><CBadge color="warning">停機中</CBadge></h3>
      </CHeaderNavItem>
      <CHeaderNavItem v-for="item in arrErrMsg" :key="item.id" class="d-md-down-none mx-1">
        <h3 v-if="bShow" class="blink_me"><CBadge color="danger">{{item}}</CBadge></h3>
      </CHeaderNavItem>
      <!-- <CHeaderNavItem class="px-3">
        <CHeaderNavLink to="/main">
          主畫面
        </CHeaderNavLink>
      </CHeaderNavItem>
      <CHeaderNavItem class="px-3">
        <CHeaderNavLink to="/input" exact>
          輸入資料
        </CHeaderNavLink>
      </CHeaderNavItem>
      <CHeaderNavItem class="px-3">
        <CHeaderNavLink to="/unfinish">
          載入未完記錄
        </CHeaderNavLink>
      </CHeaderNavItem>
      <CHeaderNavItem class="px-3">
        <CHeaderNavLink to="/errorrecord">
          重量異常記錄
        </CHeaderNavLink>
      </CHeaderNavItem>
      <CHeaderNavItem class="px-3">
        <CHeaderNavLink to="/records">
          歷史資料
        </CHeaderNavLink>
      </CHeaderNavItem> -->
    </CHeaderNav>
    <CHeaderNav class="mr-4">
      <!-- <CHeaderNavItem class="d-md-down-none mx-2">
        <CHeaderNavLink>
          <CIcon name="cil-bell"/>
        </CHeaderNavLink>
      </CHeaderNavItem>
      <CHeaderNavItem class="d-md-down-none mx-2">
        <CHeaderNavLink>
          <CIcon name="cil-list"/>
        </CHeaderNavLink>
      </CHeaderNavItem>
      <CHeaderNavItem class="d-md-down-none mx-2">
        <CHeaderNavLink>
          <CIcon name="cil-envelope-open"/>
        </CHeaderNavLink>
      </CHeaderNavItem> -->
      
      <TheHeaderDropdownAccnt/>
    </CHeaderNav>
    <!-- <CSubheader class="px-3">
      <CBreadcrumbRouter class="border-0"/>
    </CSubheader> -->
  </CHeader>
</template>

<script>
import TheHeaderDropdownAccnt from './TheHeaderDropdownAccnt'

export default {
  name: 'TheHeader',
  components: {
    TheHeaderDropdownAccnt
  },
  data() {
    return {
      line_name: '',
      status: 0,
      arrErrMsg: [],
      bShow: false
    }
  },
  mounted() {
    this.getStatus()
    setInterval(() => {
      this.getStatus()
    }, 2000);
  },
  methods: {
    getStatus() {
      // 加入網址的判斷，當頁面位於各設備的頁面時，才顯示去query, display產線名稱與狀態
      let href = window.location.href
      let arr = href.split('/')
      if(arr.findIndex(x => x==='equipment') === -1) {
        this.bShow = false
        return
      }

      this.bShow = true
      
      let param = {mac: this.$store.state.plc_mac}
      this.line_name = this.$store.state.line_name
      
      this.$http.get('/api/getLastMacData', {params: {mac: this.$store.state.plc_mac}})
      .then(res => {
        if(res.data.length>0) {
          this.status = res.data[0].D600

          this.$http.get('/api/getErrorState', {params: {mac: this.$store.state.plc_mac}})
          .then(res => {
            this.arrErrMsg = []
            for(let i=0; i<res.data.length; i++) {
              this.arrErrMsg.push(res.data[i].msg)
            }
          })
          .catch(err => { console.log(`getErrorState(): ${err}`) })
        }
      })
      .catch(err => { console.log(`getLastMacData(): ${err}`) })
    }
  }
}
</script>

<style>
.blink_me {
  animation: blinker 3s linear infinite;
}

@keyframes blinker {
  50% {
    opacity: 0.4;
  }
}
</style>
